:root {
  --blue-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  text-transform: uppercase;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  height: 60px;
}

.nav_item {
  margin: 0 20px 0 0;
  position: relative;
}

.nav_list > .nav_item:last-child {
  margin: 0;
}

.nav_list {
  margin: 0;
  padding-inline-start: 0;
  list-style-type: none;
  display: flex;
}

.nav_link {
  cursor: default;
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}

.nav_link:hover {
  color: var(--blue-color);
}

